















































.userInfo .u-header {
  padding-bottom: 30px;
  position: relative;
  border-bottom: 1px solid #ccc;
  background: #fcf8e3;
}
.userInfo .u-header .ava {
  width: 90%;
  margin: 0 auto;
  padding-top: 20px;
  text-align: center;
}
.userInfo .u-header .ava img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
}
.userInfo .u-header .ava p {
  line-height: 20px;
  font-size: 15px;
}
.userInfo .u-body {
  padding: 10px;
  height: auto !important;
  height: 300px;
  min-height: 300px;
}
.userInfo .u-body .info {
  line-height: 20px;
}



