
































































.login_wrap{
	padding: 100px 50px;
}
.login_wrap label{
	display: block;
	width: 80%;
}
.login_wrap label input{
	border: 1px solid #ccc;
	border-radius: 4px;
	height: 30px;
	line-height: 30px;
	font-size: 14px;
	padding: 0 5px;
}
.login_wrap .validTip{
	color: #bf4055;
	padding: 5px;
}
.login_wrap input[type="submit"]{
	background: #dddddd;
	padding: 5px 10px;
	border-radius: 3px;
	margin-top: 10px;
	font-size: 14px;
}



